<template>
  <div class="box">
    <Header/>
    <div class="content">
      <div class="address">
        <ul @click="address()">
          <li>
            <div v-if="flag" class="dizhi">
              <div class='location'>
                <i class="iconfont icon-shouhuodizhi"></i>
              </div>
              <div class="detail">
                <span class="kd">默认</span><span>{{addresslist.name}}</span> <span>{{addresslist.tel}}</span>
                <p>{{addresslist.province + addresslist.city + addresslist.county + addresslist.addressDetail}}</p>
              </div>
              <div class="back">
                <i class="iconfont icon-qianjin"></i>
              </div>
            </div>
            <div class="addto" v-else>
              <i class="iconfont icon-xinjiandizhi2"></i><span>新增地址</span>
            </div>
          </li>
        </ul>
        <img src="http://cdn.jisuapp.cn/static/webapp/images/xcx-goods/goods-address-bg.png" alt="">
      </div>
      <div class="shop">
        <ul>
          <li v-for="item of orderlist" :key="item.proid">
            <van-card
              :num="item.num"
              :price="item.price"
              desc=""
              :title="item.proname"
              :thumb="item.proimg"/>
          </li>
        </ul>
      </div>
      <div class="meg">
        <van-cell-group>
          <van-cell title="商品金额" :value="'￥' + (totalPrice / 100).toFixed(2)" value-class="red"/>
          <van-cell title="运费" value="00.00" />
          <van-cell title="给卖家留言" value="留言" label="描述信息" />
        </van-cell-group>
      </div>
    </div>
    <footer class="footer">
      <van-submit-bar
        :price="totalPrice"
        button-text="提交订单"
        @submit="onSubmit"
      />
    </footer>
  </div>
</template>

<script>
import axios from '@/utils/request'
import Header from '@/components/Header'
import Vue from 'vue'
import { Card, Stepper, Cell, CellGroup, SubmitBar, Dialog } from 'vant'
Vue.use(Card)
Vue.use(Stepper)
Vue.use(Cell).use(CellGroup)
Vue.use(SubmitBar)
Vue.use(Dialog)
export default {
  data () {
    return {
      orderlist: [],
      flag: true,
      addresslist: []
    }
  },
  created () {
    let userid = localStorage.getItem('userid')
    let orderid = this.$route.query.orderid
    axios.get('/order?userid=' + userid + '&orderid=' + orderid).then(res => {
      this.orderlist = res.data.data[0].list
    })
    axios.get('/address/defaultaddress?userid=' + userid).then(res => {
      // console.log(res)
      if (res.data.code === '30000') {
        // 没有默认的地址
        this.flag = false
      } else {
        this.flag = true
        this.addresslist = res.data.data[0]
        console.log(this.addresslist)
        let address = this.addresslist.province + this.addresslist.city + this.addresslist.county + this.addresslist.addressDetail
        console.log(this.addresslist.name)
        let url = '/order/addinfo?orderid=' + orderid + '&name=' + this.addresslist.name + '&tel=' + this.addresslist.tel + '&address=' + address
        console.log(url)
        axios.get(url).then(res => {
          console.log('地址添加至订单')
        })
      }
    })
  },
  computed: { // 计算属性
    totalPrice () {
      let price = 0
      this.orderlist.map((item) => {
        item !== '' ? price += item.num * item.price * 100 : price += 0
      })
      return price
    }
  },
  components: {
    Header
  },
  methods: {
    address () {
      let userid = localStorage.getItem('userid')
      this.$router.push('/address?userid=' + userid)
    },
    onSubmit () {
      if (this.flag) {
        this.$router.replace('/payment?totalPrice=' + this.totalPrice + '&orderid=' + this.$route.query.orderid)
      } else {
        Dialog.alert({
          message: '请选择收货地址'
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/lib/reset.scss';
.box{
  .red{color:red;}
  @include rect(100%,100%);
  .content{
    @include background-color(#f3f3f3);@include rect(100%,100%);
    .address{
      @include background-color(#fff);
      ul{
        @include background-color(#fff);
        li{
          @include rect(100%,.7rem);
          .dizhi{
             @include rect(100%,.7rem);
            @include flexbox();@include justify-content(space-around);
            @include align-items();
          }
          .addto{
             @include rect(100%,.7rem);
            @include rect(100%,auto);font-size: .25rem;@include flexbox();@include align-items();@include justify-content();padding:15px 15px;
            .icon-xinjiandizhi2{font-size: .27rem;margin-right:.1rem;}
          }
          .location{
            i{display:block;font-size:38px;margin-left:15px;}
          }
          .detail{
            width: 2.7rem;
            padding:0 3px 0 0;
            span{
              font-size: .17rem;color:#333;display: inline-block;vertical-align: middle;margin-right:.08rem;
            }
            .kd{background: rgb(212, 166, 86);color:#fff;font-size: .12rem;padding: 0 2px;}
            p{overflow: hidden;@include rect(100%,auto);}
          }
        }
      }
      img{display: block; width: 100%;}
    }
    .shop{padding:.07rem 0;@include rect(100%,auto);position: relative;
      ul{@include rect(100%,auto);
        li{
          margin:.1rem 0;
        }
      }
    }
    .meg{
      margin-bottom: 20px;
    }
  }
}
</style>
